/*Main Button*/
.metaportal_fn_protected input[type="submit"],
.metaportal_fn_button{
	display: block;
	width: fit-content;
	max-width: 100%;
	text-decoration: none;
	font-weight: 500;
	font-size: 14px;
	letter-spacing: .5px;
	font-family: var(--hff);
	height: 50px;
	line-height: 50px;
	padding: 0 34px;
	position: relative;
	color: var(--hc);
	text-transform: uppercase;
	text-align: center;
	white-space: nowrap;
	background-color: transparent;
	outline: none;
	outline-color: transparent;
}
.metaportal_fn_button span{
	position: relative;
	z-index: 5;
}
.metaportal_fn_protected .submit{
	display: block;
	position: relative;
}
.metaportal_fn_protected .submit input{
	width: 100%;
	z-index: 5;
}
.metaportal_fn_button.full{
	width: 100%;
	height: 80px;
	line-height: 80px;
}
.metaportal_fn_protected .submit:hover:after,
.metaportal_fn_button:hover:after{
	animation-duration: 2s;
}
.metaportal_fn_protected .submit:hover,
.metaportal_fn_button:hover{
	color: #fff;
}
.metaportal_fn_protected .submit:after,
.metaportal_fn_button:after{
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	border-radius: 6px; 
	border: 3px solid transparent;
	background: -moz-linear-gradient(45deg,var(--mc1),var(--mc2),var(--mc1),var(--mc2)) border-box;
	background: -webkit-linear-gradient(45deg,var(--mc1),var(--mc2),var(--mc1),var(--mc2)) border-box;
	background: linear-gradient(45deg,var(--mc1),var(--mc2),var(--mc1),var(--mc2)) border-box;
	-webkit-mask:
	linear-gradient(#fff 0 0) padding-box, 
	linear-gradient(#fff 0 0);
	-webkit-mask-composite: destination-out;
	-webkit-mask-composite: source-out;
	mask-composite: exclude;
	background-size: 300% 300%;
	-webkit-animation: animatedgradient 4s ease alternate infinite;
	animation: animatedgradient 4s ease alternate infinite;
}
.metaportal_fn_protected .submit:before,
.metaportal_fn_button:before{
	right: 0;
    bottom: 0;
    top: 0;
    left: 0;
    position: absolute;
    color: var(--mc1);
    content: '';
    opacity: .15;
    border-radius: 6px;
    box-shadow: 0px 5px 20px;
}
@keyframes animatedgradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}












/* Totop Button */
.metaportal_fn_totop{
	display: block;
	margin: 0;
	padding: 0;
	text-decoration: none;
	position: fixed;
	z-index: 5;
	right: 30px;
	bottom: 30px;
	width: 50px;
	color: #bbb;
	font-size: 18px;
	letter-spacing: 0;
	font-family: var(--hff);
	font-weight: 500;
	opacity: 0;
	visibility: hidden;
	transform: translateX(100px);
	transition: all .5s ease;
}
.totop-active .metaportal_fn_totop{
	transform: translateX(0);
	opacity: 1;
	visibility: visible;
}
.metaportal_fn_totop .icon{
	display: flex;
	width: 37px;
	height: 37px;
	border-radius: 100%;
	background-color: white;
	position: relative;
	margin-right: 30px;
	align-items: center;
	justify-content: center;
}
.metaportal_fn_totop .fn__svg{
	position: absolute;
	display: block;
	z-index: 3;
	width: 20px;
	height: 20px;
	left: 50%;
	top: 50%;
	margin: -10px 0 0 -10px;
	-moz-transform: rotate(-90deg);
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
}
.metaportal_fn_totop .icon:after{
	content: '';
	position: absolute;
	z-index: -1;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	color: var(--mc1);
	opacity: .35;
	border-radius: 100%;
	box-shadow: 0px 5px 20px;
	-moz-transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
	transition: all .3s ease;
}
.metaportal_fn_totop:hover .icon:after{
	opacity: .5;
}
.metaportal_fn_totop .totop_inner{
	-moz-transform: translateX(-50%) translateY(-50%) rotate(-90deg);
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(-90deg);
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
	top: 50%;
	left: 50%;
    position: absolute;
    width: max-content;
    -ms-align-items: center;
    align-items: center;
}




