	opacity: .5;
}
.frenify_cards_gallery .img_holder:before{
	border: 1px solid rgba(255,255,255,.5);
	content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: 20px;
}
.frenify_cards_gallery .img_holder:after{
	content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    color: var(--mc1);
    opacity: .15;
    border-radius: 20px;
